  margin-top: 30px;
  margin-bottom: 15px;
}

p{
  color: 'gray';
}
.fancy-cards{
  text-align: center;
  margin: 50px auto;
}
.fancy-cards .fancy-card{
  display: inline-block;
  position: relative;
  top: 40px;
  left: 20px;
  box-sizing: border-box;
}

  .fancy-card .top,
  .fancy-card .middle,
  .fancy-card .bottom{
    position: relative;
    border-radius: 3px;
    overflow: hidden;
    width: 400px;
    height: 350px;
    transition: transform 300ms linear-out;
    
  }
  .fancy-card .top{
    z-index: 3;
    transform: scale(1.0);
    transition: transform 300ms cubic-bezier(0.22, 0.61, 0.36, 1);
  
    background-size: cover;
    background-position: center;
    box-shadow: 0px 1px 3px rgba(25,25,25,0.30);
  }
  .fancy-card .middle{
    position: absolute;
    background: #aaa;
    top: 0px;
    z-index: 2;
    transform: rotate(0deg);
    transition: transform 250ms cubic-bezier(0.68, -0.55, 0.27, 1.55);
  }
  .fancy-card .bottom{
    position: absolute;
    background: #ccc;
    top: 0px;
    z-index: 1;
    transform: rotate(0deg);
    transition: transform 250ms cubic-bezier(0.68, -0.55, 0.27, 1.55);
  }
  
  .fancy-card .caption{
    overflow: hidden;
    background: rgba(255,255,255,0.75);
    padding: 15px 10px;
    position: absolute;
    bottom: 0px;
    left: 0px;
    width: 100%;
  }
  .fancy-card .caption .title{
    color: #222;
    margin: 0px 0px 15px 0px;
    font-size: 1.4rem;
  }
  .fancy-card .caption .button{
    display: inline-block;
    color: #333;
    text-decoration: none;
    border: solid 1px #555;
    padding: 7px 13px;
    background-color: transparent;
    transition: all 300ms ease-in;
  }
  
  
  /*hovering*/
  .fancy-card:hover .top{
    transform: scale(1.05);
  }
  .fancy-card:hover .middle{
   transform: rotate(-7deg);
   box-shadow: 1px 1px 2px rgba(74, 74, 74, 0.35);
  }
  .fancy-card:hover .bottom{
    transform: rotate(7deg);
    box-shadow: 1px 1px 2px rgba(113, 113, 113, 0.35);
  }
  
  .fancy-card:hover .button{
    background: rgba(0,0,0,0.8);
    color: #fff;
    border: 0px;
  }
  



.social {
    font-size: 35px;
    
    // FontAwesome
    .fa {
      margin: 12px 6px;
      position: relative;
      transition: .3s all ease;
      top: 0;
      &.fa-link {color: rgb(15, 203, 216);}
      &.fa-github-square {color: rgb(0, 0, 0);}
      &.fa-linkedin-square {color: #007bb6;}
      &.fa-facebook-square {color: #007bb6;}
      &.fa-instagram {color: #b60064;}
      &.fa-envelope-o {color:rgb(55, 57, 58);}
     
      

      &:hover {
        top: -4px;
      }
    }

  }


  




